import personalInfo from '../../components/personalInfo.vue'
import personalList from '../../components/personalList.vue'

export default {
	components:{
		personalInfo,personalList
	},
	data() {
		return {
			list:[],
			show:'',
			block:"#000000"
		}
	},
	onLoad() {
		this.getVideoInfo()
	},
	methods: {
		getVideoInfo(){
			uni.request({
				url:'http://127.0.0.1:8088/static/json/videos.json',
				method:'GET',
				success: (res) => {
					this.list = res.data.list
				}
			})
		}
	}
}
